input, select { 
    padding: 0;
    margin: 0;
    outline: none;
    font-family: Roboto, Arial, sans-serif;
    font-size: 18px;
    color: rgb(215, 76, 16);
    }
    h1, h3 {
    font-weight: 400;
    }
    h1 {
    font-size: 32px;
    }
    h3 {
    color: #1c87c9;
    }
    .main-block, .info {
    display: flex;
    flex-direction: column;
    }
    .main-block {
    justify-content: center;
    align-items: center;
    width: 100%; 
    min-height: 100%;
    background: url("https://plus.unsplash.com/premium_photo-1665394006686-d2549607649a?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1172&q=80") no-repeat center;
    background-size: cover;
    }
    form {
    width: 80%; 
    padding: 75px;
    margin-bottom: 60px;
    background: rgba(0, 0, 0, 0.9);
    }
  
    .postmeal{
      color: #eee;
    }
    input, select {
    padding: 15px;
    margin-bottom: 20px;
    background: transparent;
    border: none;
    border-bottom: 1px solid #eee;
    }
    input::placeholder {
    color: #eee;
    }
    option {
    background: black; 
    border: none;
    }
    .metod {
    display: flex; 
    }
  
    .btn2 {
    display: block;
    margin-top: 30em;
    width: 200px;
    padding: 10px;
    margin: 20px auto 0;
    border: none;
    border-radius: 5px; 
    background: #1c87c9; 
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    }
    btn2:hover {
    background: #095484;
    }
    @media (min-width: 568px) {
    .info {
    flex-flow: row wrap;
    justify-content: space-between;
    }
    input {
    width: 46%;
    }
    input.fname {
    width: 100%;
    }
    select {
    width: 48%;
    }
}