* {
    margin: 0;
    padding: 0;
    
  }
  
  article {
    width: 60%;
    margin: auto;
    background: url("https://images.unsplash.com/photo-1582738411706-bfc8e691d1c2?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8OHx8d2hpdGUlMjBiYWNrZ3JvdW5kfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60") no-repeat center;
    background-size: cover;
  }
  
  .cart_box img {
    width: 150px;
    height: 210px;
  
    padding: 1em;
  }
  .cart_img {
    display: flex;
    width: 400px;
  }
  
  .cart_img p {
    font-weight: bold;
    font-style: italic;
    font-size: larger;
    font-family: cursive;
    margin-left: 12px;
    margin-top: 70px;
    font-size: 2em;
    color: rgb(19, 18, 17);
  }
  
  .cart_box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    border-bottom: 4px solid rgb(18, 17, 16);
    padding-bottom: 5px;
  }
  
  .cart_box div:nth-child(2) button {
    padding: 5px 10px;
    font-weight: bold;
    margin-right: 5px;
  }
  
  .cart_box div:nth-child(3) button {
    padding: 5px 10px;
    font-weight: bold;
    letter-spacing: 2px;
    border: none;
    outline: none;
    color: rgb(223, 77, 28);
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    cursor: pointer;
    
  }
  .cart_box div:nth-child(3) button:hover {
    background-color:whitesmoke;
  }
  .cart_box div:nth-child(3) span {
    background-color: whitesmoke;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 10px;
  }
  
  .total {
    display: flex;
    justify-content: space-between;
  }
  
  .total span:nth-child(1) {
    font-size: 2rem;
    color: rgb(23, 26, 27);
    font-weight: bold;
    letter-spacing: 2px;
  }
  
  .total span:nth-child(2) {
    font-size: 2rem;
    color: rgb(223, 77, 28);
    font-weight: bold;
  }
  
  table {
    border-collapse: collapse;
    max-width: 900px;
    width: 100%;
    margin: 2rem auto;
  }
  table th, table td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  
  .checkout{
    width: 200px;
    display:inline-block;
    padding:0.7em 1.7em;
     margin:0 0.3em 0.3em 0;
     border-radius: 100px;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:400;
    color:#fefafa;
    background-color:#0d0d0f;
    /* box-shadow:inset 0 -0.6em 1em -0.35em rgba(0,0,0,0.17),inset 0 0.6em 2em -0.3em rgba(255,255,255,0.15),inset 0 0 0em 0.05em rgba(255,255,255,0.12); */
    text-align:center;
    position:relative;
  }
  
  /* .checkout:active{
    box-shadow:inset 0 0.6em 2em -0.3em rgba(0,0,0,0.15),inset 0 0 0em 0.05em rgba(255,255,255,0.12);
    } */
    @media all and (max-width:30em){
    .checkout.button7{
    display:block;
    margin:0.4em auto;
    }
    }
    @keyframes star {
      0% {
        margin-left: -8px;
        margin-top: -8px;
      }
      25% {
        margin-left: 194px;
        margin-top: -8px;
      }
  
      50% {
        margin-left: 194px;
        margin-top: 75px;
      }
      75% {
        margin-left: -8px;
        margin-top: 75px;
      }
      100% {
        margin-left: -8px;
        margin-top: -8px;
      }
    }
  
    .cart_box div:nth-child(3) span {
      width: 150px;
      height: 50px;
      cursor: pointer;
      font-size: 20px;
      font-weight: bold;
      font-family: 'Staatliches', cursive;
      letter-spacing: 5px;
      color:black;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 0px rgba(0,0,0,0.4);
      transition: 500ms;
    }
    
    
    .my_btn button {
      width: 50px;
      height: 50px;
      cursor: pointer;
      border: none;
      font-family: 'Comfortaa', cursive;
      color: rgba(53, 41, 41, 0.5);
      font-size: 20px;
      border-radius: 4px;
      box-shadow: inset 0px 3px 5px rgba(28, 26, 26, 0.5), 0px 0px 10px rgba(0,0,0,0.15);
      background: rgb(2,0,36);
    background: linear-gradient(45deg, rgba(2,0,36,0) 5%, rgba(255,255,255,.5) 6%, rgba(255,255,255,0) 9%, rgba(255,255,255,.5) 10%, rgba(255,255,255,0) 17%, rgba(20, 19, 19, 0.5) 19%, rgba(9, 7, 7, 0) 21%);
      background-size: 150%;
      background-position: right;
      transition: 1s;
    }
    
    .my_btn button:hover{
      background-position: left;
      color: rgb(25, 23, 23);
      box-shadow: inset 0px 3px 5px rgba(255,255,255,1), 0px 0px 10px rgba(0,0,0,0.25);
    }
    
    .my_btn button:focus {
      outline: none;
    }